*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: #111;
    color: #fff;
}

a{
    color: inherit;
    text-decoration: none;

    &:hover{
        color: inherit;
    }
}

li{
    list-style: none;
}

.App{
    max-width: 1140px;
    margin-left: auto;
    margin-right: auto;
}

.main{
    background-color: #191919;
}