.header {
    position: relative;

    .header__logo {
        width: 35%;
        padding: 21px 0;
    }

    .nav {
        display: flex;
        height: 50px;
        line-height: 50px;
        justify-content: space-between;
        background-color: #505453;

        &>ul {
            display: flex;
            padding-left: 0;
            margin-bottom: 0;

            li {

                &.active,
                &:hover {
                    background-color: #484c4b;
                }

                a {
                    padding: 0 14px;
                }
            }
        }

        .search__form {
            display: flex;
            color: #fff;
        }

        .nav__search {
            outline: none;
            border: none;
            border-radius: 0;
            background-color: #484c4b;
            padding: 17px 14px;
            height: 50px;
            color: inherit;
        }

        .nav__searchBtn {
            padding: 0 19px;
            background-color: #484c4b;
            height: 50px;
            outline: none;
            border: none;
            transform: scale(.9);
            position: relative;
            background-color: #484c4b;
            box-shadow: 0px 0px 7px -2px white;
            color: inherit;
        }
    }

    .platforms {
        display: flex;
        justify-content: center;
        align-items: center;
        box-shadow: 0 9px 14px -7px #fff;
        background: linear-gradient(to bottom, #6a7574 20%, #0d0a06 100%);
        padding: 8px;
        height: 58px;
        box-sizing: content-box;

        &>* {
            width: 43px;
            border-radius: 11px;
            margin: 0 4px;
        }
    }

    .filter {
        padding: 21px;
        background-color: #191919;

        &>ul {
            padding-left: 0;
            margin-bottom: 0;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;

            li.filter__genre {
                margin: 4px;

                &.active {
                    order: -1;

                    button {
                        background: linear-gradient(to bottom, #b39494 18%, #3a8383 100%);
                        color: #2c1f1f;
                    }

                    .filter__removeIcon {
                        display: block;
                    }
                }

                button {
                    background: linear-gradient(to bottom, #b5c3ed 18%, #06f7f7 100%);
                    border-radius: 10px;
                    min-width: 50px;
                    font-weight: 800;
                    cursor: pointer;
                    color: #5c5c5c;
                    margin: 4px -8px -4px 8px;
                    padding: 10px;
                    display: flex;
                    align-items: center;
                    font-size: 11px;
                    text-transform: uppercase;
                    border: none;
                    outline: none;

                    .filter__removeIcon {
                        font-size: 13px;
                        margin-left: 4px;
                        display: none;
                    }
                }
            }
        }
    }
}